@import 'assets/styles/_variables.scss';
[class^='pie'] {
  stroke-width: 58;
  fill: none;
  transition: all ease 450ms;
}
[class^='pie']:hover {
  stroke-width: 70;
  border-radius: 4px;
}
[class^='line-'] {
  stroke-width: 1;
  stroke: white;
  transform-origin: center;
  transform: scale(0);
  opacity: 0;
  transition: all ease 450ms 1500ms;

  &.line-1 {
    transition-delay: 800ms;
  }
  &.line-2 {
    transition-delay: 850ms;
  }
  &.line-3 {
    transition-delay: 900ms;
  }
  &.line-4 {
    transition-delay: 950ms;
  }
  &.line-5 {
    transition-delay: 1000ms;
  }
  &.line-6 {
    transition-delay: 1050ms;
  }
  &.line-7 {
    transition-delay: 1100ms;
  }
  &.line-8 {
    transition-delay: 1150ms;
  }
}
[class^='text-'] {
  font-family: redhatRegular;
  font-weight: 400;
  opacity: 0;
  color: white;

  transition: opacity ease 1450ms;
  &.text-1 {
    transition-delay: 800ms;
  }
  &.text-2 {
    transition-delay: 850ms;
  }
  &.text-3 {
    transition-delay: 900ms;
  }
  &.text-4 {
    transition-delay: 950ms;
  }
  &.text-5 {
    transition-delay: 1000ms;
  }
  &.text-6 {
    transition-delay: 1050ms;
  }
  &.text-7 {
    transition-delay: 1100ms;
  }
  &.text-8 {
    transition-delay: 1150ms;
  }
}

.outline {
  stroke-width: 68;
  stroke: #fff;
  fill: none;
  opacity: 0;
  transition: all ease 450ms 1000ms;
}

.pie-1 {
  stroke-dasharray: 0 628.32;
  stroke-dashoffset: 498;
  stroke: #e44345;
  transition-delay: 25ms;
}
.pie-2 {
  stroke-dasharray: 0 628.32;
  stroke-dashoffset: 490;
  stroke: #f7941e;
  transition-delay: 50ms;
}
.pie-3 {
  stroke-dasharray: 0 628.32;
  stroke-dashoffset: 560;
  stroke: #ffde16;
  transition-delay: 75ms;
}
.pie-4 {
  stroke-dasharray: 0 628.32;
  stroke-dashoffset: -69;
  stroke: #0ba14b;
  transition-delay: 100ms;
}
.pie-5 {
  stroke-dasharray: 0 628.32;
  stroke-dashoffset: 10;
  stroke: #13a89e;
  transition-delay: 125ms;
}
.pie-6 {
  stroke-dasharray: 0 628.32;
  stroke-dashoffset: 81;
  stroke: #1563a1;
  transition-delay: 150ms;
}
.pie-7 {
  stroke-dasharray: 0 628.32;
  stroke-dashoffset: 111;
  stroke: #7f3f98;
  transition-delay: 200ms;
}
.pie-8 {
  stroke-dasharray: 0 628.32;
  stroke-dashoffset: 90;
  stroke: #d399c5;
  transition-delay: 250ms;
}

.text-head {
  font-size: 30px;
  font-weight: 600;
}
.text-lbl {
  font-size: 12px;
  text-transform: uppercase;
}

.animated {
  [class^='data'],
  [class^='line-'],
  [class^='text-'],
  .outline {
    opacity: 1;
  }
  [class^='line-'] {
    transform: scale(1);
  }
  .pie-1 {
    stroke-dasharray: 208 628.32;
  }
  .pie-2 {
    stroke-dasharray: 100 628.32;
  }
  .pie-3 {
    stroke-dasharray: 85 628.32;
  }
  .pie-4 {
    stroke-dasharray: 85 628.32;
  }
  .pie-5 {
    stroke-dasharray: 80 628.32;
  }
  .pie-6 {
    stroke-dasharray: 40 628.32;
  }
  .pie-7 {
    stroke-dasharray: 36 628.32;
  }
  .pie-8 {
    stroke-dasharray: 8 628.32;
  }
}

#wrap {
  max-width: 100%;
  //border: 1px solid #3a3a3a;
}
.chart {
  width: 445px;
  aspect-ratio: 1;
}
@media (max-width: '600px') {
  #wrap {
  }
  .text-head {
    font-size: 16px;
    font-weight: 600;
    color: white !important;
  }
  .text-lbl {
    font-size: 10px;
    text-transform: uppercase;
  }
}
